pageheight + ";\n            background-color:rgba(50,50,00,0.5);\n            max-width: 20em;\n          }\n\n          span.comment::before{\n            content: '';\n            position: absolute;\n            right: 1em;\n            top: -15px;\n            display: block;\n            width: 0;\n            height: 0;\n            border-right: 15px solid transparent;\n            border-bottom: 15px solid rgba(70,70,00,0.9);\n            border-left: 15px solid transparent;\n          }\n\n          span.commentbody{\n              margin:0.5em 1em;\n              writing-mode:lr-tb;\n              font-family:sans-serif;\n              font-size:0.8em;\n              line-height:1;\n          }\n  \n      }\n        </style>\n      <link rel=\"stylesheet\" href=\"\">\n  </head>\n  <body>\n  " + mytext + "\n  \n  <script>\n  \n  setTimeout( (function(){\n      var width = document.body.clientWidth;\n      var cursor = document.getElementById('cursor');\n      var panelWidth = window.innerWidth;\n      var scrollEnd = cursor.offsetLeft - width + (panelWidth / 2);\n      window.scrollTo( scrollEnd , scrollEnd);\n      console.log(width, cursor, panelWidth, scrollEnd);\n  }), 1);\n\n</script>\n  </body>\n  </html>";
